.sidebar{
	display: -ms-grid;
	display: grid;
	-ms-grid-columns: 1fr;
	grid-template-columns: 1fr;
	-ms-grid-rows: 1fr 1px 3rem;
	grid-template-rows: 1fr 1px 3rem;
	
	border-right: 1px solid #faa4b4;
	
	z-index: 1;
	
	.button{
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-webkit-align-items: center;
		    -ms-flex-align: center;
		        align-items: center;
		
		margin: 0.5rem;
		
		border: 1px solid #cf98a3;
		-webkit-box-shadow: -1px -1px 3px #e76b81 inset;
		        box-shadow: -1px -1px 3px #e76b81 inset;
		border-radius: 5%;
		
		font-weight: bold;
		color: #1f1f21;
		text-decoration: none;
		
		cursor: pointer;
		
		&.active {
			-webkit-box-shadow: 1px 1px 4px #be7784 inset;
			        box-shadow: 1px 1px 4px #be7784 inset;
		}
		
		svg {
			fill: #f4667f;
		}
	}
	
	.line{
		margin: 0 0.5rem;
		background-color: #faa4b4;
	}
}
.sidebar > *:nth-child(1){
	-ms-grid-row: 1;
	-ms-grid-column: 1
}
.sidebar > *:nth-child(2){
	-ms-grid-row: 2;
	-ms-grid-column: 1
}
.sidebar > *:nth-child(3){
	-ms-grid-row: 3;
	-ms-grid-column: 1
}

@media screen and (orientation: portrait) {
	.sidebar{
		-ms-grid-columns: 1fr 1px 5rem;
		grid-template-columns: 1fr 1px 5rem;
		-ms-grid-rows: 1fr;
		grid-template-rows: 1fr;
		
		border-top: 1px solid #faa4b4;
		
		.line{
			margin: 0.5rem 0;
		}
	}
	.sidebar > *:nth-child(1){
		-ms-grid-row: 1;
		-ms-grid-column: 1
	}
	.sidebar > *:nth-child(2){
		-ms-grid-row: 1;
		-ms-grid-column: 2
	}
	.sidebar > *:nth-child(3){
		-ms-grid-row: 1;
		-ms-grid-column: 3
	}
}