.category{
	display: flex;
	
	.category-button{
		justify-content: space-evenly;
		
		width: 100%;
		
		font-size: 0.6875rem;
		
		svg {
			width: 1.2rem;
			height: 1.2rem;
		}
	}

	
	
	.category_popup{
		position: fixed;
		
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: stretch;
		
		left: 5rem;
		bottom: 0;
		
		max-height: 100%;
		max-width: 100%;
		
		background-color: #fff;
		
		border: 1px solid #faa4b4;
		border-radius: 0.125rem;
		
		&.show{
			width: 255px;
			height: 360px;
			
			transition: width var(--transition-speed), height var(--transition-speed);
		}
		&.hide{
			width: 0;
			height: 0;
			
			transition: width var(--transition-speed), height var(--transition-speed);
		}
		
		.category_item{
			display: flex;
			justify-content: space-between;
			align-items: center;
			
			margin: 0 0.5rem;
			padding: 0.3125rem;
			
			font-weight: bold;
			font-size: 0.75rem;
			text-decoration: none;
			
			border-radius: 0.125rem;
			
			&.txt--white{
				color: #fff;
				fill: #fff;
			}
			&.txt--eerieblack{
				color: #1f1f21;
				fill: #1f1f21;
			}
			
			&.bg--quicksilver{
				background-color: #a1a1a1;
			}
			&.bg--lavenderfloral{
				background-color: #b38dd9;
			}
			&.bg--newyorkpink{
				background-color: #d98d8d;
			}
			&.bg--tan{
				background-color: #d9b38d;
			}
			&.bg--greenyellowcrayola{
				background-color: #d9d98d;
			}
			&.bg--grannysmithapple{
				background-color: #8dd98d;
			}
			&.bg--middlebluegreen{
				background-color: #8dd9d9;
			}
			&.bg--aero{
				background-color: #8db3d9;
			}
			&.bg--lightgray{
				background-color: #cccccc;
			}
			&.bg--platinum{
				background-color: #e3e3e3; 
			}
			
			svg {
				width: 0.75rem;
			}
			
			.popup_info{
				position: fixed;
				top: 0;
				left: 0;
				
				display: flex;
				justify-content: center;
				align-items: center;
				
				width: 100%;
				height: 100%;
				
				background-color: rgba(0,0,0,0.2);
				backdrop-filter: blur(1px);
				
				pointer-events: none;
				
				&.showinfo{
				}
				&.hideinfo{
					display: none;
				}
				
				.info{
					width: 70%;
					height: 80%;
					background-color: white;
					
					color: #1f1f21;
					
					border-radius: 10px;
				}
				
			}
		}
	}
}

@media screen and (orientation: portrait) {
	.category{
	
		.category_popup{
			left: auto;
			right: 0;
			bottom: 3.5rem;
			
			.category_item{
			
			}
		}
	}
}