.app{
	display: -ms-grid;
	display: grid;
	-ms-grid-columns: 5rem 1fr;
	grid-template-columns: 5rem 1fr;
	-ms-grid-rows: 1fr;
	grid-template-rows: 1fr;
	
	background-color: #fff;
	/* background-image: linear-gradient(to bottom, #fff 0%, #fdd 100%); */
}.app > *:nth-child(1){
	-ms-grid-row: 1;
	-ms-grid-column: 1;
}.app > *:nth-child(2){
	-ms-grid-row: 1;
	-ms-grid-column: 2;
}

@media screen and (orientation: portrait) {
	.app{
		-ms-grid-columns: 1fr;
		grid-template-columns: 1fr;
		-ms-grid-rows: 1fr 3.5rem;
		grid-template-rows: 1fr 3.5rem;
		
	}
	.app > *:nth-child(1){
		-ms-grid-row: 1;
		-ms-grid-column: 1;
	}
	.app > *:nth-child(2){
		-ms-grid-row: 2;
		-ms-grid-column: 1;
	}
}