.animal-page {
	display: flex;
	align-content: flex-start;
	flex-wrap: wrap;
	
	width: 100%;
	height: 100%;
	
	.animal-gallery {
		display: flex;
		justify-content: center;
		align-items: center;
		
		width: calc(100% / 3);
		height: 11rem;
		
		overflow: visible;
		
		.animal-card {
			width: calc(100% - 0.75rem);
			height: 10rem;
			
			max-width: 24rem;
			
			background-color: #fff;
			
			border-radius: 1rem;
			
			box-shadow: 0px 5px 30px rgba(34, 35, 58, 0.5);
		}
	}
	

	@media only screen and (max-width: 68rem) {
		.animal-gallery {
			width: 50%;
		}
	}

	@media only screen and (max-width: 42rem) {
		.animal-gallery {
			width: 100%;
		}
	}
	
	
	.map-contain{
		display: flex;
		align-items: center;
		justify-content: center;
		
		width: 90%;
		height: 90%;
		
		background-color: #fff;
		
		box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.5);
		
		
		svg {
			width: 80%;
			height: 80%;
			background-color: #b8e1ff;
		}
		
	}
	
}