*{
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	overflow: hidden;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none
}

html{
	font-size: 16px;
}

body {
  font-family: sans-serif;
}

#root {
	--transition-speed: 300ms;
}